body {
  color: #222222;
  font-family: "M PLUS 1p";
}

.container {
  width: 100%;
}
@media (min-width: 520px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 960px) {
  .container {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
  }
}

header {
  background-color: #3c0000;
  color: #ffffff;
}
header .container {
  justify-content: space-between;
}
header h1 {
  margin-top: 10px;
  margin-bottom: 10px;
}
header h1 img {
  object-fit: contain;
  object-position: left;
  max-height: 50px;
  width: 100%;
}
header .menu {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
header .menu ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}
header .menu ul li {
  border-bottom: solid #ffacff 3px;
}
header .menu ul li + li {
  margin-left: 10px;
}
header .menu ul li a {
  color: #ffffff;
  font-weight: bold;
  padding: 5px;
}

footer {
  background-color: #222222;
  color: #ffffff;
  padding-top: 10px;
  padding-bottom: 10px;
}
footer .lower {
  border-top: solid #ffacff 1px;
}

.wpsisac-slick-carousal .wpsisac-image-slide {
  margin: 0;
}

.wpsisac-slick-carousal .slick-dots li button {
  border-color: #ff93ff;
}
.wpsisac-slick-carousal .slick-dots li.slick-active button {
  background-color: #ffacff !important;
}

.wpsisac-image-fit .wpsisac-image-slide-wrap img,
.wpsisac-image-fit .wpsisac-slide-wrap img {
  object-fit: contain;
  object-position: center;
}

.slick-container {
  padding: 10px 0;
  background-color: #c2ffff;
}

.front-about {
  padding-top: 30px;
  padding-bottom: 30px;
}
.front-about h2 {
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.front-about h2::after {
  display: block;
  content: "";
  width: 50px;
  border-bottom: solid #008664 1px;
  margin: 20px auto;
}
.front-about ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  flex-wrap: wrap;
  margin: 0 10px;
}
@media (min-width: 520px) {
  .front-about ul {
    flex-wrap: nowrap;
    margin: 0;
  }
}
.front-about ul li {
  flex-basis: calc(50% - 5px);
  padding: 15px;
  border-radius: 5px;
  border: solid #c2ffff 1px;
  display: flex;
  flex-direction: column;
  transition: border-color 0.5s ease-in-out;
  margin-bottom: 10px;
}
@media (min-width: 520px) {
  .front-about ul li {
    flex-basis: calc((100% - 30px) / 4);
  }
}
.front-about ul li:hover {
  border-color: #ffacff;
}
.front-about ul li h3 {
  text-align: center;
  margin-bottom: 10px;
}
.front-about ul li p {
  text-align: justify;
  font-weight: lighter;
  font-size: 12px;
  font-size: 0.75rem;
}
.front-about ul li .button-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.front-about ul li .button-container a {
  display: block;
  margin: 15px auto 0;
  padding: 5px;
  background-color: #c2ffff;
  color: #3c0000;
  text-decoration: none;
  width: 100px;
  border-radius: 20px;
  text-align: center;
  font-size: 12px;
  font-size: 0.75rem;
  transition: background-color 0.5s ease-in-out, color 0.5s ease-in-out;
}
.front-about ul li .button-container a:hover {
  background-color: #008664;
  color: #ffffff;
}

